//CARDS

/*cards*/

.card__container {
	background-color: $bg;
}

.cards__collection {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	justify-content: center;
	align-items: center;
}

.card__item {
	background-color: white;
	margin: 15px;
	border-radius: 5px;
	box-shadow: 0px 2px 9px 0px rgba(133, 112, 96, 0.15);
	width: 360px;
	@include bp-480 {
		width: 100%;
	}
}

/*-----------*/


/*Card info*/

.card__pic {
	img {
		width: 100%;
		display: block;
	}
}

.card__info {
	display: flex;
	flex-direction: column;
}

.spec {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	padding: 25px;
}

.spec__block {
	position: relative;
	span {
		display: inline-block;
		padding-left: 15px;
		font-size: 14px;
		color: rgb(124, 115, 109);
		line-height: 1.714;
		&:before {
			position: absolute;
			top: 4px;
			left: 0;
			content: '';
			width: 10px;
			height: 10px;
			background: url('./../img/ic-check.png') no-repeat;
		}
	}
}

.second-spec {
	padding-left: 15px;
	display: inline-block;
	position: relative;
	&:before {
		position: absolute;
		top: 2px;
		left: 0;
		content: '';
		width: 10px;
		height: 10px;
		background: url('./../img/ic-check.png') no-repeat;
	}
}

.price {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
	padding: 0 25px 10px 25px;
	@include bp-480 {
		flex-direction: column;
		padding-bottom: 18px;
	}
}

.price__cost {
	display: flex;
	align-items: center;
	@include bp-480 {
		margin-bottom: 10px;
	}
}

.good-cost {
	font-size: 38px;
	font-family: 'lora', serif;
	font-weight: bold;
	margin-right: 10px;
	span {
		font-size: 26px;
	}
}

.bad-cost {
	font-size: 18px;
	position: relative;
	&:after {
		position: absolute;
		content: '';
		top: 10px;
		left: 0;
		width: 100%;
		height: 1px;
		background-color: $dark;
		transform: rotate(-7deg);
	}
}

.price__select {
	select {
		width: 130px;
		height: 34px;
		border-radius: 5px;
		background-color: #F4F1F1;
		border: none;
		font-weight: bold;
		cursor: pointer;
	}
}

.video-review {
	text-align: center;
	a {
		position: relative;
		padding-left: 20px;
		font-size: 14px;
		color: rgb(191, 151, 124);
		&:before {
			position: absolute;
			content: '';
			top: 4px;
			left: 0;
			width: 14px;
			height: 14px;
			background: url('./../img/camera-ic.png') no-repeat;
		}

	}
}

.buttons {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
	text-align: center;
	padding: 15px 25px 25px 25px;
	@include bp-480 {
		flex-direction: column;
		padding-top: 20px;
	}
}

.btn-gallery {
	font-size: 18px;
	padding: 12px 30px;
	color: $dark;
	border: 1px solid $link;
	border-radius: 30px;
	transition: 0.3s;
	&:hover {
		background-color: #F7E7DE;
		border-color: #F7E7DE;
		text-decoration: none;
		color: $dark;
	}
	@include bp-480 {
		margin-bottom: 15px;
		font-size: 16px;
		width: 100%;
		padding-top: 8px;
		padding-bottom: 8px;
	}
}

.btn-order {
	font-size: 18px;
	font-weight: bold;
	padding: 12px 45px;
	border: none;
	border-radius: 30px;
	background-color: $red;
	color: $white;
	transition: 0.3s;
	&:hover {
		background-color: #f35555;
		border-color: #f35555;
		text-decoration: none;
		color: $white;
	}
	@include bp-480 {
		width: 100%;
	}
}

/*-----------*/


// END ==========

