//HEADER-BANNER

/*Left Block*/

.banner__container {
	background-color: $bg;
	overflow: hidden;
}

.header-wrap {
	display: flex;
	@include bp-768 {
		flex-direction: column;
		justify-content: center;
	}
}

.left-block {
	width: 550px;
	padding-top: 220px;
	padding-bottom: 220px;
	margin-right: 60px;
	span {
		font-size: 26px;
		text-transform: uppercase;
		color: #87827E;
	}
	h1 {
		font-size: 50px;
		font-weight: bold;
		margin-bottom: 30px;
	}
	p {
		font-size: 24px;
		line-height: 1.2;
		margin-bottom: 70px;
	}
	@include bp-768 {
		margin-right: 0;
		width: 100%;
		text-align: center;
		padding-top: 80px;
		padding-bottom: 80px;
		p {
			margin-bottom: 50px;
		}
	}
	@include bp-480 {
		padding-top: 40px;
		padding-bottom: 40px;
		span {
			font-size: 18px;
		}
		h1 {
			font-size: 28px;
			margin-top: 5px;
			margin-bottom: 15px;
		}
		p {
			font-size: 18px;
			margin-bottom: 20px;
		}
	}

}

.btn-container {
	display: flex;
	flex-direction: row;
	align-items: center;
	a {
		font-size: 26px;
		font-weight: bold;
		color: $dark;
		transition: 0.3s;
		&:hover {
			color: #7c706e;
			text-decoration: none;
		}
	}
	@include bp-768 {
		justify-content: center;
	}
	@include bp-640 {
		flex-direction: column;
	}
}

.main-btn {
	@include button(transparent, #321E1F);
	margin-right: 20px;
	@include bp-640 {
		margin-right: 0;
		margin-bottom: 15px;
	}
	@include bp-480 {
		width: 200px;
		height: 45px;
		font-size: 14px;
	}
}


/*-----------*/


/*Right Block*/

.right-block {
	position: relative;
	img {
		position: absolute;
		width: 1000px;
	}
	@include bp-768 {
		img {
			position: relative;
			width: 100%;
			height: auto;
		}
	}
}

/*-----------*/


// HEADER END ==========


