//PAYMENT

/*Main container*/

.block-title {
	font-size: 38px;
	text-align: center;
	margin-bottom: 20px;
	@include bp-480 {
		h2 {
			font-size: 24px;
		}
	}
}


/*-----------*/


/*Logo block*/

.payment-block {
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
	@include bp-768 {
		flex-direction: column;

	}
}

.small-logo__block {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	width: 500px;
	@include bp-768 {
		width: 480px;
	}
	@include bp-480 {
		width: 100%;
		justify-content: center;
	}
	
}

.small-logo__item {
	width: 100px;
	display: flex;
	justify-content: center;
	align-items: center;
	height: 100px;
	margin: 10px;
	border-radius: 10px;
	box-sizing: border-box;
	padding: 20px;
	background-color: $white;
	box-shadow: 0px 2px 18px 0px rgba(133, 112, 96, 0.06);
}

.courier {
	display: flex;
	flex-direction: row;
	align-items: center;
	width: 400px;
	height: 220px;
	border-radius: 10px;
	padding: 30px;
	background-color: $white;
	overflow: hidden;
	box-shadow: 0px 2px 18px 0px rgba(133, 112, 96, 0.06);
	p {
		min-width: 105px;
	}
	@include bp-768 {
		margin: 10px;
		width: 470px;
		height: 120px;
	}
	@include bp-480 {
		width: 220px;
		img {
			position: relative;
			width: 200px;
			left: - 40px;
		}
	}
}


