footer {
	overflow: hidden;
}

.footer__left {
	display: flex;
	flex-direction: row;
}

.footer__right {
	position: relative;
	img {
		position: absolute;
		top: -80px;
		width: 1000px;
		height: auto;
	}
	@include bp-1200 {
		img {
			display: none;
		}
	}
}

.notfound-block {
	display: flex;
	flex-direction: column;
	margin-right: 50px;
	h2 {
		font-size: 32px;
		font-weight: bold;
		margin-bottom: 10px;
	}
	p {
		font-size: 20px;
		margin-bottom: 20px;
	}
	@include bp-1200 {
		width: 100%;
		align-items: center;
		margin-right: 0;
		text-align: center;
	}
	@include bp-480 {
		h2 {
			font-size: 24px;
		}
		p {
			font-size: 16px;
		}
	}
}

.advantages {
	display: flex;
	flex-direction: row;
	align-items: center;
	margin-bottom: 20px;
}

.advantage__item {
	display: flex;
	align-items: center;
	margin-top: 10px;
	margin-right: 30px;
	margin-bottom: 10px;
	img {
		margin-right: 15px;
	}
	span {
		font-size: 16px;
		color: rgb(67, 58, 52);
		line-height: 1.2;
	}
	@include bp-640 {
		flex-direction: column;
		margin: 15px;
		img {
			margin-right: 0;
			margin-bottom: 10px;
		}
	}
	@include bp-480 {
		span {
			font-size: 14px;
		}
	}
}

.footer__contacts {
	display: flex;
	flex-direction: row;
	align-items: center;
	@include bp-640 {
		flex-direction: column;
		a {
			margin-right: 0;
		}
	}
}

.footer__phone {
	font-size: 26px;
	font-weight: bold;
	color: $dark;
	margin-right: 15px;
	transition: 0.3s;
	&:hover {
		color: #7c706e;
		text-decoration: none;
	}
	@include bp-640 {
		margin-bottom: 15px;
	}
}

.footer__btn {
	@include button(transparent, $dark);
	font-size: 13px;
	width: 190px;
	height: 35px;
	margin-right: 15px;
	@include bp-640 {
		margin-right: 0;
		margin-bottom: 20px;
	}
}

.social-btn {
	a {
		margin: 5px;
		img {
			width: 35px;
		}
	}
}

.padding-small {
	padding-top: 40px;
	padding-bottom: 40px;
	background-color: #EBEBEB;
	@include bp-480 {
		padding-top: 30px;
		padding-bottom: 30px;
	}
}

.badprice-block {
	display: flex;
	flex-direction: column;
	h2 {
		font-size: 32px;
		font-weight: bold;
		margin-bottom: 10px;
	}
	p {
		font-size: 20px;
		margin-bottom: 20px;
	}
	@include bp-1200 {
		align-items: center;
	}
	@include bp-640 {
		text-align: center;
	}
	@include bp-480 {
		h2 {
			font-size: 24px;
		}
		p {
			font-size: 16px;
		}
	}
}

.badprice-btn {
	@include button(transparent, $dark);
	@include bp-480 {
		width: 200px;
		height: 45px;
		font-size: 14px;
	}
}