//PRESENT

/*Name block*/

.present__container {
	background: url('./../img/present-bg.jpg') no-repeat;
	background-size: cover;
	background-color: #D0AA87;
}

.present-wrap {
	display: flex;
	flex-direction: column;
	align-items: center;
}


/*-----------*/


/*present info*/

.present__info {
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
	margin-bottom: 40px;
	@include bp-1200 {
		flex-direction: column;
	}
}

.present__info-block {
	display: flex;
	flex-direction: column;
	@include bp-1200 {
		align-items: center;
	order: 2;

	}
}

.present__form {
	@include bp-1200 {
		form {
			width: 500px;
		}
		div {
			width: 100%;
			margin-bottom: 15px;
		}
		input {
			width: 100% !important;
			margin: 0 !important;
		}
		button {
			width: 100% !important;
		}
	}
	@include bp-640 {
		form {
			width: 400px;
		}
	}
	@include bp-480 {
		width: 100%;
		form {
			width: 100%;
		}
	}
}

.present__img-block {
	@include bp-1200 {
		order: 1;
		margin-bottom: 20px;
	}
	@include bp-480 {
		img {
			width: 280px;
		}
	}
}


.info__data {
	display: flex;
	flex-direction: row;
	align-items: center;
	margin-bottom: 30px;
	@include bp-640 {
		flex-direction: column;
	}
}

.info__data-block {
	display: flex;
	flex-direction: row;
	align-items: center;
	@include bp-640 {
		margin-top: 20px;
		order: 2;
	}

}

.data__day {
	position: relative;
	padding-left: 35px;
	margin-right: 20px;
	&:before {
		content: '';
		position: absolute;
		top: 2px;
		left: 0;
		width: 23px;
		height: 23px;
		background: url('./../img/ic-data.png') no-repeat;
	}
	p {
		font-size: 14px;
		color: $white;
	}
	span {
		font-size: 20px;
		color: $white;
	}
	@include bp-320 {
		span {
			display: block;
		}	
	}
}

.data__time {
	position: relative;
	padding-left: 35px;
	margin-right: 20px;
	&:before {
		content: '';
		position: absolute;
		top: 1px;
		left: 0;
		width: 23px;
		height: 23px;
		background: url('./../img/ic-time.png') no-repeat;
	}
	p {
		font-size: 14px;
		color: $white;
	}
	span {
		font-size: 20px;
		color: $white;
	}
	@include bp-480 {
		margin-right: 0;
	}
}

.winner {
	display: flex;
	flex-direction: row;
	align-items: center;
	img {
		margin-right: 15px;
	}
}

.winner__name {
	span {
		display: block;
		font-size: 11px;
		color: $white;
	}
	a {
		font-size: 18px;
		color: $white;
		&:hover {
			color: rgb(255, 215, 189);
			text-decoration: none;
		}
	}
}

.info__title {
	margin-bottom: 30px;
	h2 {
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		font-size: 38px;
		color: $white;
		font-weight: bold;
		line-height: 1.5;
	}
	span {
		padding: 7px 20px;
		background-color: #F9C8A0;
	}
	@include bp-1200 {
		h2 {
			align-items: center;
			text-align: center;
		}
	}
	@include bp-480 {
		h2 {
			font-size: 24px;
		}
	}
}

/*-----------*/


/*info condition*/

.info__condition {
	display: flex;
	flex-direction: row;
	align-content: center;
	@include bp-640 {
		flex-direction: column;
	}
	@include bp-480 {
		div:first-child {
			margin-right: 0 !important;
			margin-bottom: 30px;
		}
	}
}

.info__condition div:first-child {
	margin-right: 50px;
}

.condition__item {
	display: flex;
	flex-direction: row;
	align-items: center;
	span {
		font-size: 56px;
		color: rgb(247, 201, 170);
		font-weight: bold;
		margin-right: 10px;
	}
	img {
		width: 35px;
		height: 35px;
		margin-right: 10px;
	}
	p {
		font-size: 16px;
		color: $white;
		line-height: 1.2;
	}
	@include bp-640 {
		margin-bottom: 10px;
	}
	@include bp-480 {
		flex-direction: column;
		text-align: center;
		span {
			margin-right: 0;
			font-size: 30px;
		}
		img {
			display: none;
		}
	}

}

.condition__item-text {
	width: 150px;
	@include bp-640 {
		width: 100%;
	}
}

.condition__info {
	p {
		font-size: 16px;
		color: $white;
		margin-bottom: 10px;
	}
}

.condition__info-btn {
	display: flex;
	flex-direction: row;
	align-items: center;
	@include bp-480 {
		flex-direction: column;
	}
}

.insta {
	font-size: 22px;
	color: $white;
	margin-right: 15px;
	transition: 0.3s;
	&:hover {
		color: rgb(255, 215, 189);
		text-decoration: none;
	}
	@include bp-480 {
		margin-bottom: 15px;
	}
}

.info-btn {
	color: $dark;
	background-color: $white;
	padding: 10px 30px;
	border-radius: 20px;
	transition: 0.3s;
	&:hover {
		background-color: #F7E7DE;
		color: $dark;
		text-decoration: none;
	}
}

/*-----------*/


/*form*/

.form {
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: center;
	width: 100%;
	padding: 20px;
	border: 10px solid #DBBB9E;
	border-radius: 10px;
	input {
		width: 220px;
		height: 45px;
		margin: 5px;
		padding-left: 40px;
		border-radius: 50px;
		border: none;
		background-color: $white;
		font-size: 13px;
	}
	button {
		display: inline-block !important;
		@include button($dark, transparent);
		width: 260px;
		height: 45px;
		margin: 5px;
		font-size: 16px;
		color: $white;
	}
	@include bp-1200 {
		flex-direction: column;
	}
}

.form__ic-insta {
	position: relative;
	&:before {
		position: absolute;
		content: '';
		top: 38%;
		left: 22px;
		background: url('./../img/ic-insta.png') no-repeat;
		width: 12px;
		height: 13px;
	}
}

.form__ic-mail {
	position: relative;
	&:before {
		position: absolute;
		content: '';
		top: 42%;
		left: 22px;
		background: url('./../img/ic-mail.png') no-repeat;
		width: 12px;
		height: 9px;
	}
}

.form__ic-phone {
	position: relative;
	&:before {
		position: absolute;
		content: '';
		top: 37%;
		left: 22px;
		background: url('./../img/ic-phone.png') no-repeat;
		width: 9px;
		height: 14px;
	}
}

