//PHOTOS

/*Name block*/

.photos__container {
	background: url('./../img/portfolio-bg.jpg') no-repeat;
	background-size: cover;
}

.photos-block {
	display: flex;
	flex-direction: row;
	background-color: $white;
	padding: 25px;
	height: inherit;
	@include bp-480 {
		flex-direction: column;
	}
}

.photos-wrap {
	flex: 1 1 300px;
	margin: 15px;
	img {
		max-width: 100%;
		height: auto;
	}
	@include bp-480 {
		width: auto;
	}
}

