//ABOUT

/*About*/

.about__container {
	background-color: #EDEDED;
}

.about-wrap {
	display: flex;
	flex-direction: row;
	justify-content: center;
	@include bp-768 {
		flex-direction: column;
	}
}

.about-block {
	display: flex;
	flex-direction: row;
	@include bp-640 {
		justify-content: center;
	}
}

.about-pic {
	@include bp-640 {
	display: none;	
	}
}

.about-text {
	padding: 40px;
	@include bp-640 {
		padding: 0;
		padding-bottom: 40px;
	}
	@include bp-480 {
		padding-bottom: 20px;
	}

}

.about__title {
	margin-bottom: 50px;
	h2 {
		position: relative;
		padding-top: 60px;
		&:before {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			width: 280px;
			height: 110px;
			background: url('./../img/order-logo.png') no-repeat;
		}
	}
	@include bp-640 {
		text-align: center;
		margin-bottom: 20px;
		h2:before {
			left: 50px;
		}
	}
	@include bp-480 {
		h2 {
			font-size: 24px;
		}
	}
	@include bp-320 {
		h2 {
			&:before {
				left: 20px;
			}
		}
	}
}

.about__points {
	display: flex;
	flex-direction: column;
}

.about__item {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
	padding: 20px 30px;
	margin: 5px 0;
	border-radius: 5px;
	cursor: pointer;
	transition: 0.3s;
	background-color: $white;
	p {
		font-size: 18px;
		color: rgb(116, 107, 101);
		margin-right: 25px;
	}
	&:hover {
		background-color: #F7E7DE;
	}
}

.green-call__block {
	display: flex;
	@include bp-768 {
		justify-content: center;
	}
}

.green-call {
	align-self: flex-end;
	padding: 30px;
	border-radius: 50%;
	background-color: #4FE778;
	box-shadow: 0px 0px 40px 0px rgba(64, 235, 110, 0.69);
	transition: 0.3s;
	cursor: pointer;
	&:hover {
		background-color: #45d66c;
	}
	@include bp-480 {
		padding: 15px;
		img {
			width: 25px;
		}
	}
}


