//QUOTE

/*quote*/



.quote {
	display: flex;
	flex-direction: column;
	align-items: center;
	width: 450px;
	margin: 0 auto;
	padding-top: 100px;
	padding-bottom: 100px;
	img {
		width: 50px;
		height: 35px;
	}
	p {
		font-size: 32px;
		color: rgb(67, 58, 52);
		line-height: 1.2;
		text-align: center;
	}
	@include bp-768 {
		padding-top: 50px;
		padding-bottom: 50px;
	}
	@include bp-480 {
		width: 240px;
		p {
			font-size: 20px;
		}
	}
}



